
<template>
   <div class="content" ref="content">
       <ul class="list" v-for="(ele,index) in listData" :key="index">
           <li class="list-title">{{ele['gc_name']}}</li>
           <li class="list-item"v-for="(data,i) in ele['next_goods_class']" :key="i"
           @click="getId(data['gc_id'])"
           >
               <p      class="item">{{data['gc_name']}}</p> 
           </li>
       </ul>
   </div>
</template>

<script>

export default {
    name: 'pr-classity',
    props:{
        listData:{
            type:Array,
            default(){
                return []
            }
        }
    },
    methods:{
        getId(id){
         this.$emit("get-id",id)
    }
  }
   
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.content{
    overflow: hidden;
    width: 100%;
}
.list{
    float: left;
    width: 100%;
}
.list-title{
    height: 35px;
    line-height: 35px;
    color: #fff;
    font-weight: 600;
    border-bottom: 1px #eee solid;
    border-top: 1px #eee solid;
    background: #ffa500;
    opacity: 0.6;
    text-align: center;
}
.list-item{
    width: 25%;
    height: 40px;
    float: left;
    line-height: 40px;
    text-align: center;
    overflow: hidden;
    font-size: 14px;
}

</style>


