<template>
	<div class="header">
        <ul>
            <li class="header-btn">
            	<slot name="left">
                <a href="javascript:history.go(-1);">&lt;</a>
               </slot>
            </li>
            <li class="header-tit">
               	{{title}}
            </li>
            <li  class="header-btn">
	            	<slot name="right">
	            		消息
	            	</slot>
            </li>
        </ul>
   </div>
</template>

<script>
	export default {
	  	name: 'my-header',
	  	props:{
		    title:{
		     	type:String,
		      	default:"头部标题"
		    }
		 }
	}
</script>

<style scoped>
	
	.header { height: 50px; position: absolute; width: 100%;
     line-height: 50px;  left: 0; top: 0; background: #ffa500;z-index: 10;color: #fff;font-size: 20px;}
    .header  ul { display: flex;text-align: center}
    .header-tit { flex: 1}
    .header-btn {width: 60px;}
    .header-btn a{ display: block;color: #fff;}
</style>


