<template>
   <div id="homelist">
       <div class="header-wrap">
          <div class="head">
                  <input type="text" v-model="content" placeholder="搜索商品">
                  <a class="head-btn" href="javascript:;" @click="Search"></a>
          </div>
      </div>
       <my-footer></my-footer>
       <my-content>
           <p class="store_name">{{StoreName}}</p>
           <div class="evaluation">
               <span>综合评分：</span>
               <span class="back"></span>
               <span><em>5</em>分</span>
           </div>
         <div class="list">
            <p v-if="notData">暂无商品</p>
             <ul
                v-infinite-scroll="loadMore"
                infinite-scroll-disabled="loading"
                infinite-scroll-distance="10">
                   <li class="item"  v-for="(ele,index) in ListData" 
                                     :key="index" 
                                     :id="ele.goods_id"
                                     @click="EntryDetails(ele.goods_id)">
                        <img  v-lazy="ele.goods_image_url" alt="" >
                        <p>{{ele.goods_name}}</p>              
                        <p class="price">￥<span>{{ele.goods_price}}</span></p> 
                    </li>
            </ul>    
        </div>
       </my-content>
   </div>
</template>

<script>
export default {
  name: 'my-homelist',
  data () {
    return {
         allLoaded: false,
         StoreName:"hahah",
         content:"",
         notData:false,
         ListData:[],
         PageNum:6
    }
  },
  methods:{
        EntryDetails(id){   
        //点击商品跳转至详情页；
                this.$router.push({path:"/homedetail",query:{goods_id:id}})     
        },    
        loadMore() {
        //加载更多  
            this.loading = true;
            this.getList()
        },
        Search(){
        //点击搜索功能
          this.$router.push({path:"/homelist",query:{keyword:this.content}})
          this.getList(()=>{
                if(!this.ListData.length){
                    this.notData= true
                }else{
                    this.notData= false
                }
          })
        },
        getList(c){ 
           this.PageNum +=6
           const {store_id,store_name,keyword}= this.$route.query
           this.StoreName = store_name

           var url =`/mobile/index.php?act=store&op=goods_list&page=${this.PageNum}&store_id=${store_id}&keyword=${keyword}`
           this.$http.get(url)
           .then(res=>{            
                  this.ListData = res.data.datas.goods_list
                  c&&c()
           }) 
        }
  },
    mounted(){   
           this.getList()
    }
}
</script>
<style scoped>
.header-wrap {width: 100%; height: 50px; line-height: 48px;background: #ffa500;position: relative;}
.head{ padding-left: 15px;}
.head input{ height: 30px;border: 0px;width:100%;text-indent: 8px;vertical-align: middle;font-weight: normal;font-size: 14px;}
.head .head-btn{ display: block;background: url("http://m.mallvv.com/wap/images/icon.png") no-repeat;
            position: absolute;top: 9px;right: 0px;height: 32px;width: 50px;background-color: #ffa500; 
            background-position: 13px -123px;background-size: 60px auto;}
.store_name{width:100%;padding: 0 10px;font: 600 14px/30px arial,"microsoft yahei";color: #555;}
.evaluation{width:100%;line-height: 40px;padding: 0 10px;font-size: 10px;background: #fff;
           border-bottom: solid 1px #E6E6E6;border-top: solid 1px #E6E6E6;}
.evaluation .back{background: url("http://m.mallvv.com/wap/images/2014grate.png") no-repeat 100% 0;
                 display: inline-block; width: 79px;height: 15px;}
 .evaluation em{color: #DA542E;font-weight: 600; vertical-align: middle;margin-right: 2px;}              
.list{width:100%;padding: 5px;background: #fff;box-sizing: border-box;}
.list ul{width:100%;overflow: hidden;}
.list .item{width:100%;padding: 5px;border-bottom: 1px solid #e8e5e5;overflow: hidden;}
.list .item img{width:100px;height:100px;float: left;margin-right: 10px;}
.list .item p{float:left;width:60%;line-height: 20px; }
.list .item .price{margin-top: 10px; color: #ffa500;}
</style>



