<template>
  <div class="page">
        <my-header title="商品列表">
             <a  slot="right"></a>
        </my-header>
        <my-footer></my-footer>
        <my-content>
            <div class="list">
                <ul   v-infinite-scroll="loadMore"
                        infinite-scroll-disabled="loading"
                        infinite-scroll-distance="10">
                    <li class="item" v-for="(ele,index) in listData" :key="index" 
                    @click="getId(ele['goods_id'])"
                    >
                        <p class="img"><img v-lazy="ele['goods_image_url']"></p>
                        <div  class="text">
                            <p>{{ele['goods_name']}}</p>
                            <span>￥{{ele['goods_price']}}</span>
                        </div>
                    </li>
                </ul>
            </div>
        </my-content>
  </div>
</template>
<script>

export default {
    name: 'list',
    data(){
        return{
            listData:[],
            num:6
        }
    },
    methods:{
        loadMore(){
            this.loading = true;
            this.getData()
            
        },
        getData(){
            const id = this.$route.query.id;
            this.num+=6;
            //console.log(this.num)
            var url=`/mobile/index.php?act=goods&op=goods_list&key=4&page=${this.num}&curpage=1&gc_id=${id}`
            this.$http.jsonp(url)
            .then(res=>{
                this.listData=res.body.datas['goods_list']
                //console.log(res.body.datas['goods_list'])
            })
        },
        getId(id){
        this.$router.push({path:"/homedetail",query:{goods_id:id}}) 
        console.log(id);
    }

    },
    mounted(){
       this.getData()
        
    }
}
</script>
<style scoped>
.list{
    overflow: hidden;
    background: #fff;
}
.list .text span{
    display: inline-block;
    line-height: 30px;
    color: #ffa500;
    font-weight: 600;

}
</style>



