<template>
   <div id="home-list">
      <my-header title="返回商品">
        
         <span slot="right"></span>
      </my-header>
    
       <my-content>
          <div id="vhtml" v-html="Datas">
            
          </div>
       </my-content>
   </div>
</template>

<script>
export default {
    name: 'my-homepagedetail',
    data () {
    return {
            Datas:""
    }
    },
    mounted(){  
        const {goods_id}= this.$route.query
        console.log(goods_id)
        this.$http.get(`/mobile/index.php?act=goods&op=goods_body&goods_id=${goods_id}`)
        .then(res=>{
            console.log(res)
            this.Datas =res.data
        }) 
       
         console.log(this.Datas)
    }
}
</script>

<style scoped>
#home-list{
    width:100%;overflow: hidden;
}
p{
    width:100%;
}
p img{
    width:100%;
}
</style>



