<template>
	<div class="register">
		<my-header title="登录">
			<a href="#/register" slot="right">
				注册
			</a>
		</my-header>
		<my-content>
			<div class="content">
				<ul>
					<li class="user-name">
						<span>用户名: </span>
						<input placeholder="用户名" ref="userID"/>
					</li>
					<li class="password">
						<span>密 码: </span>
						<input type="password" placeholder="请输入密码" ref="password"/>
					</li>
					
				</ul>
				<button class="reg-btn" @click="login">登录</button>
			</div>
		</my-content>
	</div>
</template>

<script>
	import { Toast } from 'mint-ui';
	export default {
		name:'my-login',
			
		methods:{
			login(){
				console.log(this.$refs)
				let userID = this.$refs.userID.value;
				let password = this.$refs.password.value;
				this.$http.get(`http://datainfo.duapp.com/shopdata/userinfo.php?status=login&&userID=${userID}&&password=${password}`)
				.then(res=>{
					if(res.body==0){
						Toast({
							  message: '用户名不存在',
							  iconClass: 'icon icon-success'
							});
					}else if(res.body==2){
						Toast({
							  message: '用户名密码不符',
							  iconClass: 'icon icon-success'
							});
					}else{
						Toast({
							  message: '登录成功',
							  iconClass: 'icon icon-success'
							});
						localStorage.setItem("userID",userID);
						this.$router.push('/')
					}
				})
					
			}
		},
		
	}
</script>

<style scoped>
	.register .content{
		padding:60px 10px 0;
	}
	.content li{
		text-align: left;
		border-bottom: 1px solid #ccc;
		line-height: 50px;
	}
	.content li input{
		text-indent: 10px;
		border: 0;
		outline: 0;
	}
	.content .reg-btn{
		width: 100%;
		background: #ffa500;
		height: 50px;
		line-height: 50px;
		color: #fff;
		border: 0;
		border-radius: 5px;
		margin-top:40px;
		font-size: 20px;
		outline: 0;
	}
</style>


