<template>
   <div id="home">
      <div class="header-wrap">
          <div class="head">
                  <input type="text" v-model="content" placeholder="搜索商品">
                  <a class="head-btn" href="javascript:;" @click="Search"></a>
          </div>
      </div>
       <my-footer></my-footer>
       <my-content>
         <div class="banner">
              <mt-swipe :auto="4000">
                <mt-swipe-item v-for="(ele,index) in BannerData" :key="index">
                   <img :src="ele" >
                </mt-swipe-item>
              
              </mt-swipe>
          </div>
       <my-homepagelist @entry-details="EntryDetails"  @page-details="PageEntryDetails"></my-homepagelist>
       
       </my-content>


   </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
        content:"",
        BannerData:["http://www.mallvv.com/data/upload/shop/editor/web-101-101-1.jpg?399",
                    "http://www.mallvv.com/data/upload/shop/editor/web-101-101-5.jpg?274",
                    "http://www.mallvv.com/data/upload/shop/editor/web-101-101-2.jpg?884",
                    "http://www.mallvv.com/data/upload/shop/editor/web-101-101-4.jpg?694",
                    "http://www.mallvv.com/data/upload/shop/rec_position/1085834d049b67a34650.jpg",
                    "http://www.mallvv.com/data/upload/shop/rec_position/ebe7db2ffb77bf816380.jpg",
                    "http://www.mallvv.com/data/upload/shop/rec_position/19d74fc3412e550a7180.jpg"
                   ]
    }
  },
    methods:{
        EntryDetails(id){   
          //页面跳转至详情页；
                this.$router.push({path:"/homedetail",query:{goods_id:id}})     
        },
        PageEntryDetails(id){   
          //页面跳转至详情页；
                this.$router.push({path:"/homedetail",query:{goods_id:id}})     
        },
        Search(){               
          this.$router.push({path:"/homelist",query:{keyword:this.content}})
        }
    },
    mounted(){
     
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#home{
	  background: #EBEBEB;
}
.banner{
  width:100%;height:130px;margin:15px 0;background:#fff;
}
.banner img{
  width:100%;height:100%;
}
.header-wrap {width: 100%; height: 50px; line-height: 48px;background: #ffa500;position: relative;
}
.head{ background-image: url("http://m.mallvv.com/data/upload/shop/common/home_logo.png");
        background-repeat: no-repeat;background-position: 10px 5px;padding-left: 135px;
}
.head input{ height: 30px;border: 0px;width:100%;text-indent: 8px;vertical-align: middle;font-weight: normal;font-size: 14px;}

.head .head-btn{ display: block;background: url("http://m.mallvv.com/wap/images/icon.png") no-repeat;
            position: absolute;top: 9px;right: 0px;height: 32px;width: 50px;background-color: #ffa500; 
            background-position: 13px -123px;background-size: 60px auto;
}
</style>



