<template>
	<div class="register">
		<my-header title="注册">
			<a href="javascript:;" slot="right">
				<span></span>
			</a>
		</my-header>
		<my-content>
			<div class="content">
				<ul>
					<li class="user-name">
						<span>用户名: </span>
						<input placeholder="用户名" ref="userID"/>
					</li>
					<li class="password">
						<span>密 码: </span>
						<input type="password" placeholder="请输入密码" ref="password"/>
					</li>
					<li class="rePassword">
						<span>密 码: </span>
						<input type="password" placeholder="请再次确认密码" ref="rePassword">
					</li>
				</ul>
				<button class="reg-btn" @click="register">注册</button>
			</div>
		</my-content>
	</div>
	
</template>

<script>
	import { Toast } from 'mint-ui';
	export default {
		name:'my-register',
			
		methods:{
			register(){
				let userID = this.$refs.userID.value;
				let password = this.$refs.password.value;
				let rePassword = this.$refs.rePassword.value;
				if(password!=rePassword){
					alert("两次输入的密码不一致")
				}else{
					this.$http.get(`http://datainfo.duapp.com/shopdata/userinfo.php?status=register&&userID=${userID}&&password=${password}`)
					.then(res=>{
						if(res.body==0){
							Toast({
							  message: '用户名已存在',
							  iconClass: 'icon icon-success'
							});
						}else if(res.body==2){
							Toast({
							  message: '操作失败',
							  iconClass: 'icon icon-success'
							});
						}else{
							Toast({
							  message: '注册成功',
							  iconClass: 'icon icon-success'
							});
							this.$router.push('/login')
						}
						
					})
				}
			}
		},
		
	}
	
</script>

<style scoped>
	.content{
		padding:60px 10px 0;
	}
	.content li{
		text-align: left;
		border-bottom: 1px solid #ccc;
		line-height: 50px;
	}
	.content li input{
		text-indent: 10px;
		border: 0;
		outline: 0;
	}
	.content .reg-btn{
		width: 100%;
		background: #ffa500;
		height: 50px;
		line-height: 50px;
		color: #fff;
		border: 0;
		border-radius: 5px;
		margin-top:40px;
		font-size: 20px;
		outline: 0;
	}
</style>


