<template>
	<div class="cart-list">
		<ul>
            <li v-for="(ele,index) in cartData" :key="index" v-if="ele.number" :ref="'item'+index">
            	<label>
            		<input type="checkbox" class="checked" :checked="ele.selected" @change="changeSelected({type:'item',index})"  >
            	</label>
                <img v-lazy="ele.goodsListImg">
                <div class="goods-info">
                	<p class="goods-name">{{ele.goodsName}}</p>
	                <p> ￥ {{ele.price}}</p>
	                <p>
	                    <button class="jian" @click="changeCartData({index,num:ele.number*1-1,id:ele.goodsID})">-</button>
	                    <input class="number" readonly="text" v-model="ele.number">
	                    <button class="jia" @click="changeCartData({index,num:ele.number*1+1,id:ele.goodsID})">+</button>
	                </p>
	                <button @click="changeData(0,index)" class="del">删除</button>
                </div>
            </li>
        </ul>
	</div>
</template>

<script>
	import { mapActions} from "vuex"
	export default {
	  name: 'cart-list',
	  props:{
	    cartData:{
	      type:Array,
	      default(){
	        return []
	      }
	    }
	  },
	
	  methods:{
	    ...mapActions({
	      changeSelected:"cart/changeSelected",
	      changeCartData:"cart/changeCartData"
	    })
	  }
	}
	/*export default {
		name: 'my-cart-list',
		props:{
			cartData:{
				type:Array,
				default(){
					return[]
				}
			}
		},
		methods: {
			changeData(type,index){
				this.$emit("change-data",type,index)
			}
		}
	}*/
	
	
	
</script>

<style scoped>
	.cart-list {font-size: 16px;}
	.cart-list li .select{position: absolute; left: 0;top: 50%;}
    .cart-list li {  padding: 15px; border-bottom: 1px solid #ccc;overflow: hidden;position: relative;}
    .cart-list li img { width: 130px; height: 130px;float: left;}
    .cart-list .goods-info{float: left;margin: 10px;line-height: 40px;}
    .cart-list .goods-name{width: 150px;white-space: nowrap;overflow: hidden;text-overflow: ellipsis;}
    .cart-list li .number { width: 50px;}
    .cart-list .checked{position: absolute;top: 48%;left: 0px;}
    .cart-list li button { width: 20px;margin: 0 10px;cursor: pointer}
    .cart-list li .del { width: 50px;position:absolute;right: 0px;top: 10px;}
</style>


