import Vuex from "vuex"
import Vue from "vue"
import cart from "./modules/cart"
Vue.use(Vuex);

export default new Vuex.Store({
    state:{
        allSelected:1
    },
    modules:{
        cart
    }
})
///state 放app里面的数据
//getters 帮你在state里面获取到你想要的数据，（数据解析和计算）
//metations 直接改变state里面数据的方法
//actions 异步数据交互的方法，action结束以后需要commit给metations 


//组件里面获取数据"computed" ...mapsGetters(["你要的数据"])
//你想要的方法 "methods"   ...mapActions(["你要的方法"])     调用的时候通过this.xxx


//modules 可以实现 state的私有化，
//namespaced 可以实现 getters 和mutations的私有化，但是在组件里面需要加模块前缀接收数据和方法
// ...mapActions({
//       getCartData:"cart/getCartData",
//       changeSelected:"cart/changeSelected"
//     })




























