<template>
  <div>
       <p>软装家具</p>
       <div class="Soft-furniture">
            <a  href="javascript:;" id="101664" @click = "PageEntryDetails(101664)"  ><img class="left" src="http://www.mallvv.com/data/upload/mobile/special/s0/s0_05285440139511697.jpg"></a>
            <a href="javascript:;" id="101110" @click = "PageEntryDetails(101110)"  ><img class="right" src="http://www.mallvv.com/data/upload/mobile/special/s0/s0_05285440355599171.jpg"></a>
            <a href="javascript:;" id="101064" @click = "PageEntryDetails(101064)"  ><img class="right right1" src="http://www.mallvv.com/data/upload/mobile/special/s0/s0_05285440472316468.jpg"></a>
       </div>
        <p>家装建材</p>
       <div class="Soft-furniture">
            <a href="javascript:;" id="16" @click = "PageEntryDetails(16)"  ><img class="left" src="http://www.mallvv.com/data/upload/mobile/special/s0/s0_05261236848344724.jpg"></a>
            <a href="javascript:;" id="1012" @click = "PageEntryDetails(1012)" ><img class="right" src="http://www.mallvv.com/data/upload/mobile/special/s0/s0_05261238006305739.jpg"></a>
            <a href="javascript:;" id="889" @click = "PageEntryDetails(889)" ><img class="right right" src="http://www.mallvv.com/data/upload/mobile/special/s0/s0_05261238381193077.jpg"></a>
       </div>
        <p>软装家具</p>
        <ul class="Soft-furniture1">
            <li v-for="(ele,index) in HomePageData" 
                :key="index">
                <a href="javascript:;" @click = "PageEntryDetails(101064)" ><img :src="ele.image"></a>
            </li>
        </ul>
        <p>家装建材</p>
        <ul class="Soft-furniture1">
           <li v-for="(ele,index) in HomePageData1" 
                :key="index" >
                <a href="javascript:;" @click = "PageEntryDetails(101064)" ><img :src="ele.image"></a>
            </li>
        </ul>
        <p class="home-title">软装家具</p>
        <ul class="home-list">
            <li v-for="(ele,index) in HomeData" 
                :key="index"
                :id="ele.goods_id"
                @click = "EntryDetails(ele.goods_id)">
                 <a href="javascript:;"><img :src="ele.goods_image"></a>
                <p class="name">{{ele.goods_name}}</p>
                <span></span>
                <p class="price">￥{{ele.goods_promotion_price}}</p>
            </li>
        </ul>
        <p class="home-title">建材</p>
        <ul class="home-list">
            <li v-for="(ele,index) in HomeData1" 
                :key="index"
                :id="ele.goods_id"
                @click = "EntryDetails(ele.goods_id)">
               <a href="javascript:;"><img :src="ele.goods_image"></a>
                <p class="name">{{ele.goods_name}}</p>
                <span></span>
                <p class="price">￥{{ele.goods_promotion_price}}</p>
            </li>
        </ul>     
  </div>
</template>
<script>
    export default {
        name: 'my-homepagelist',
        data () {
            return {
                type:Array,
                HomeData:[],
                HomeData1:[],
                HomePageData:[],
                HomePageData1:[]
            }
        },
        methods:{
            EntryDetails(id){    
                //调用父组件的方法;
                 this.$emit("entry-details",id)
            },
            PageEntryDetails(id){
                //调用父组件的方法;
                 this.$emit("page-details",id)
            }      
        },
        mounted(){
          
            this.$http.jsonp("http://m.mallvv.com/mobile/index.php?act=index")
            .then(res=>res.json())
            .then(data=>{
                this.HomePageData = data.datas[3].home3.item
                this.HomePageData1 = data.datas[4].home3.item   
                this.HomeData = data.datas[5].goods.item
                this.HomeData1 = data.datas[6].goods.item           
            })
        }     
    }
</script>
<style scoped>
p{width:100%;line-height:44px;border-top:1px solid #d1d1d1;background:#fff;padding:0 20px;}
.Soft-furniture{width:100%;height:150px;border-top:1px solid #d1d1d1;border-bottom:1px solid #d1d1d1;overflow: hidden;margin-bottom: 15px;}
.Soft-furniture .left{width:50%;height:100%;border-right:1px solid #d1d1d1;float: left;}
.Soft-furniture .right{width:49%;height:49.5%;float: left;}
.Soft-furniture .right1{border-top:1px solid #d1d1d1;}
.Soft-furniture1{width:100%;height:150px;overflow: hidden;margin-bottom: 15px;border-bottom:1px solid #d1d1d1;}
.Soft-furniture1 li{width:50%;height:50%;float: left;}
.Soft-furniture1 li img{width:100%;height:100%;}
.home-title{border:none;background: none;}
.home-list{width: 100%;overflow: hidden;}
.home-list li{width: 48%;overflow: hidden;background: #fff;display: inline-block; margin:1%;border-radius: 5px;box-shadow: 0 1px 2px rgba(0,0,0,0.15);}
.home-list li img{width:100%;margin-top:8px;}
.home-list li .name{width:100%;padding:8px 8px 0 8px;overflow:hidden;height:46px;border:none;background: none;box-sizing: border-box;line-height: 18px;font-size: 14px;margin-bottom: 8px;}
.home-list li .price{padding: 8px 0;color: #ffa500;border-top: dotted 1px #D1D1D1;margin:0 8px;line-height: 20px;}
</style>



