<template>
   <div id="home-list">
           <div class="banner">
              <mt-swipe :auto="4000">
                <mt-swipe-item v-for="(ele,index) in TopData" :key="index">
                   <img :src="ele" >
                </mt-swipe-item>
              </mt-swipe>
          </div>
             <div class="graphic-page page"><a @click="EntrGraphic" href="javascript:;">图文详情</a></div>
             <div class="page-list page"><a @click="EntryList" href="javascript:;">进入本店铺</a></div>
             <div class="page"><a href="javascript:;">联系方式</a></div>
             <div class="information">
                 <ul >
                     <li>价格：<span class="price">￥{{InforData.goods_price}}</span></li>
                     <li>市场价：<del class="pas_price">￥{{InforData.goods_marketprice}}</del></li>
                     <li> 销量：<span class="sell_num">{{InforData.goods_salenum}}件</span></li>
                     <li> <p>商品描述：</p><span>暂无商品描述</span></li>
                 </ul>
             </div>
             <div class="activity-warp">
                 <div class="activity">
                    <p class="top_p">活动名称：暂无</p>
                    <p>活动描述：暂无</p>
                 </div>
             </div>
              <div class="join-warp">
                 <ul class="join-cart">
                     <li><div>库存：<span>10件</span></div></li>
                     <li class="join-center">
                         <p>数量：</p>
                         <div class="num">
                             <span @click="ChangeNum(-1)">-</span>
                             <input v-model="number" >
                             <span @click="ChangeNum(1)">+</span>
                         </div>
                     </li>
                     <li class="join-btn">
                         <p @click="AddFailed">立即购买</p>
                         <p @click="AddFailed" class="btn">加入购物车</p>
                     </li>
                 </ul>
             </div>
            <p class="home-title">软装家具</p>
            <div class="list">
                <ul class="home-list">
                    <li v-for="(ele,index) in HomeListData" 
                        :key="index"
                        :id="ele.goods_id"
                        @click="EntryDetails(ele.goods_id)" 
                        >
                        <a href="javascript:;"><img :src="ele.goods_image_url"></a>
                        <p class="name">{{ele.goods_name}}</p>
                    
                        <span></span>
                        <p class="price">￥{{ele.goods_price}}</p>
                    </li>  
                </ul>
            </div>

   </div>
</template>

<script>
export default {
  name: 'my-homedetailfrom',
  data () {
    return {
        type:Array,
         TopData:[],
         InforData:[],
         HomeListData:[],
         id:"",
         number:1
    }
  },
  methods:{
        EntrGraphic(){   
        //页面跳转至详情页；
                this.$router.push({path:"/homegraphic",query:{goods_id:this.id}})     
        }, 
        EntryDetails(id){  
        //下方列表点击更换id,刷新当前页面
                document.body.scrollTop = 0;
             this.$router.push({path:"/homedetail",query:{goods_id:id}})
             this.getInitState()   
        }, 
        EntryList(){  
        //进入本店铺方法；
                 this.$emit("entry-list")
        },
         ChangeNum(type){
         //数量的加减功能;
           var num =1;
           num =  this.number + type
           num = num<1?1:num
           num = num>10?10:num
           this.number = num
        },
        AddFailed(){
            //点击立即购买和加入购物车提示失败信息;
           this.$toast("提交失败")
        },
        getInitState(){
            document.body.scrollTop = 0; 
            const {goods_id}= this.$route.query
            this.id = goods_id  
                    var url =`/mobile/index.php?act=goods&op=goods_detail&goods_id=${goods_id}`
                    this.$http.get(url)
                    .then(res=>{
                    // console.log(res)
                        this.TopData =  res.data.datas.goods_image.split(",")
                        this.InforData =  res.data.datas.goods_info 
                        this.HomeListData =  res.data.datas.goods_commend_list     
                })   
        }   
  },
    mounted(){  
         this.getInitState()   
    }
}
</script>
<style scoped>
span{color: #909090;}
.banner{width:100%;height:320px;background:#fff;padding: 10px 20px;box-sizing: border-box;}
.banner img{width:100%;height:100%;}
.page{width:100%;padding: 8px 6px 0 6px;box-sizing: border-box}
.page a{display: block;border-left: 5px solid #ffa500;color: #6d6d6d; position: relative;background: #fff;padding: 10px;}
.information{width:100%;padding: 8px 6px 0 6px;box-sizing: border-box;}
.information ul{width:100%;overflow: hidden;background: #fff;padding: 10px;box-sizing: border-box;}
.information ul li{width:100%;padding:3px 0px;box-sizing: border-box;}
.information ul .price{color: #ffa500;}
.activity-warp{padding: 8px 6px 0 6px;box-sizing: border-box;}
.activity{width:100%;padding:10px;box-sizing: border-box;background: #fff;}
.activity p{width:100%;padding: 10px 0;box-sizing: border-box;}
.activity .top_p{border-bottom: 1px dashed #ddd;}
.join-warp{padding: 8px 6px 0 6px;box-sizing: border-box;}
.join-cart{width:100%;padding:10px;box-sizing: border-box;background: #fff;}
.join-cart .join-center{border-bottom: 1px dashed #ddd;border-top: 1px dashed #ddd;}
.join-cart  li{width:100%;padding: 10px 0;box-sizing: border-box;overflow: hidden;}
.join-cart  li input{width: 50px;text-align: center;float: left; height: 36px;box-shadow: inset 0 1px 3px rgba(0,0,0,.05);border: 1px solid #e3e3e3; }
.join-cart  .join-center span{height: 36px;line-height: 36px;width: 36px; display: inline-block;text-align: center;
    background: #e3e3e3;border: 1px solid #e3e3e3;cursor: pointer;font-size:24px;font-weight: bold;color:#f11;float: left;}
.join-cart  .join-center .num{margin-top:10px;overflow: hidden}
.join-cart .join-btn{width:100%;display: flex;}
.join-cart .join-btn p{flex:1;float:left;padding:0 10px;margin-right: 10px;background: #ffa500;
    line-height:40px; text-align: center;color: #fff;}
.join-cart .join-btn .btn{background: #6cb248;}
.home-title{padding: 10px;}
.list{padding: 8px 6px 0 6px;box-sizing: border-box;}
.home-list{width: 100%;background:#fff;overflow: hidden;}
.home-list li{width: 30%;overflow: hidden;display: inline-block; margin-left:2%;}
.home-list li img{width:100%;margin-top:8px;}
.home-list li .name{width:100%;padding:2px 8px;overflow:hidden;height:40px;background: none;box-sizing: border-box;line-height: 18px;font-size: 14px;margin-bottom: 8px;}
.home-list li .price{padding: 8px 0 5px 0;color: #ffa500;line-height: 20px;}

</style>
       


