// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueResource from "vue-resource"
// 引入iconfont的样式表
import "./iconfont/iconfont.css"
// 公用组件 全局导入注册
import Content from './components/public/Content'
import Header from './components/public/Header'
import Footer from './components/public/Footer'
import HomePage from './components/HomePage/HomePageList'
import HomeDetailFrom from './components/HomePageDetail/Detailform'
import Mint from 'mint-ui'
import store from './store'









Vue.component(HomeDetailFrom.name,HomeDetailFrom)
Vue.component(HomePage.name,HomePage)
Vue.component(Content.name, Content)
Vue.component(Header.name, Header)
Vue.component(Footer.name, Footer)

Vue.config.productionTip = false

// use 使用插件
Vue.use(Mint);
Vue.use(VueResource)// vue全局使用VueResource
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})




