<template>
	<div class="user">
		<my-header title="会员中心">
			<a href="javascript:;" slot="left">
				<span></span>
			</a>
			<a href="javascript:;" slot="right">
				<span></span>
			</a>
		</my-header>
		<my-footer></my-footer>
		<my-content>
			<div class="userInfo">
				<img src="http://b222.photo.store.qq.com/psb?/V14G7yth2jmjH6/w1a96gvnJ8eXXGjD2oBd.Rg232PIG3WfKVs9kwWDFRc!/m/dN4AAAAAAAAA&amp;ek=1&amp;kp=1&amp;pt=0&amp;bo=VQNwBFUDcAQFGTo!&amp;su=0226465569&amp;tm=1498474800&amp;t=5#sce=54-11-3&amp;rf=v1_ht5_qz_3.4.0_001_idc_b-0-0&quot"/>
				<p>昵称 ： <span>{{userID}}</span></p>
				<p>积分 ： 0</p>
			</div>
			<ul class="user-wrap">
				<li><span>我的订单</span><span>></span></li>
				<a href="#/coupon"><li><span>我的优惠券</span><span>></span></li></a>
				<a href="#/youhui"><li><span>优惠活动</span><span>></span></li></a>
				<a href="#/history"><li><span>我的足迹</span><span>></span></li></a>
				<a href="#/lottery?name=11"><li><span>抽奖</span><span>></span></li></a>
				<a href="#/login" @click="loginout"><li><span>退出</span><span>></span></li></a>
			</ul>
		</my-content>
	</div>
</template>

<script>
	export default {
		name:'my-user-center',
		data(){
			return{
				userID:''
			}
		},
		methods:{
			loginout(){
				localStorage.clear();
			}
		},
		mounted(){
			this.userID = localStorage.getItem("userID")
		}
		
	}
	
	
</script>
	
<style scoped>
	.user .content{
		padding: 60px 10px 44px;
	}
	.userInfo{
		width: 100%;
		overflow: hidden;
	}
	.userInfo img{
		width:100px;
		height: 100px;
		float: left;
		margin-right: 10px;
	}
	.userInfo p{
		text-align: left;
		float: left;
		line-height: 30px;
		width: 50%;
	}
	.user-wrap{
		padding:10px;
		text-align: left;
	}
	.user-wrap li{
		width: 100%;
		line-height: 50px;
		border-bottom: 1px solid #ccc;
		text-indent: 5px;
		overflow: hidden;
		color: #666;
	}
	.user-wrap li span:nth-child(1){
		float: left;
	}
	.user-wrap li span:nth-child(2){
		float: right;
	}
</style>


