<template>
	<div class="coupon">
		<my-header title="我的优惠券">
			<a href="javascript:;" slot="right"><span></span></a>
		</my-header>
		<my-content>
			<div class="coupon-wrap">
				<img v-for="(data,index) in couponData" :key="index" :src="data"/>
			</div>
		</my-content>
	</div>
</template>

<script>
	export default{
		name:'my-coupon',
		data(){
			return{
				couponData:[]
			}
		},
		mounted(){
			this.couponData = JSON.parse(localStorage.getItem('coupon'));
			console.log(this.couponData)
		}
		
	}
</script>

<style scoped>
	.coupon{
		text-align: center;
	}
	.coupon img{
		width: 90%;
		margin: 20px 0;
	}
	
	
</style>


