<template>
	<div class="cart">
		<my-header title="购物车">
			<a href="javascript:;" slot="left">
				<span></span>
			</a>
			<a href="javascript:;" slot="right">
				结算
			</a>
		</my-header>
		<my-footer></my-footer>
		<my-content>
			<div class="count">
				<span class="amout">总数： {{amount}} 件 </span>
				<span class="price"> 总价格：￥ {{totalPrice}}</span>
			</div>
			<ul class="cart-wrap">
				<label class="allSelect">
					全选：<input type="checkbox"  :checked="allSelected"  @change="changeSelected({type:'all'})" >
				</label>
				<my-cart-list ref="cart-list" :cart-data='cartData' @change-data='changeData'></my-cart-list>
			</ul>
		</my-content>
	</div>
</template>

<script>
	import CartList from './CartList';
	import { Indicator } from 'mint-ui';
	import { MessageBox } from 'mint-ui';
	import {mapGetters, mapActions} from "vuex"
	export default {
	  name: 'cart',
	  components:{
	    "my-cart-list":CartList
	  },
	  methods:{
	    ...mapActions({
	      getCartData:"cart/getCartData",
	      changeSelected:"cart/changeSelected"
	    })
	  },
	  computed:{
	    ...mapGetters({
	      cartData:"cart/cartData",
	      allSelected:"cart/allSelected",
	      totalPrice:"cart/totalPrice"
	    }),
	    totalPrice() {
        	var sum = 0;
        	if(this.cartData.length){ 
                this.cartData.forEach((ele, index) => {
                    sum += ele.price * ele.number
                });
                return sum
        	}else{
        		return sum
        	}
        },
        amount(){
        	var cont = 0;
        	if(this.cartData.length){
            	this.cartData.forEach((ele, index) => {
                    cont += ele.number*1
                });
                return cont
            }else{
            	return cont
            }
        }	
	  },
	  mounted(){
	    this.getCartData()
	  }
	}
