<template>
	<div class="history">
		<my-header title="历史记录">
			<a href="javascript:;" slot="right"></a>
		</my-header>
		<my-content>
			<ul class="history-wrap">
				<li v-for="(data,index) in historyData" :key=index>
					<img :src="data"/>
				</li>
			</ul>
		</my-content>
	</div>
</template>

<script>
	export default{
		name:'my-history',
		data(){
			return{
				historyData:[]
			}
		},
		mounted(){
			let id =  JSON.parse(localStorage.getItem('goods_id'));
			 console.log(id)
			for(let i = 0;i < id.length; i++){
				this.$http.jsonp('http://m.mallvv.com/mobile/index.php?act=goods&op=goods_detail&goods_id='+id[i])
				.then(res=>{
					console.log(res.body.datas)
					let data = res.body.datas['goods_commend_list'][0]['goods_image_url']
					
					this.historyData.push(data)
				})
				
			}
			 //console.log(this.historyData)
		}
	}
</script>

<style>
	
	.history .history-wrap img{
		width:40%;
		float: left;
		margin: 5%;
	}
</style>


