import Vue from 'vue'
import Router from 'vue-router'

import Cart from '@/components/Cart'
import Coupon from '@/components/Coupon'
import UserCenter from '@/components/UserCenter'
import History from '@/components/History'
import Youhui from '@/components/Youhui'
import Login from '@/components/Login'
import Register from '@/components/Register'
import Lottery from '@/components/Lottery'
import Home from '@/components/HomePage/index'
import HomePageDetail from '@/components/HomePageDetail/index'
import HomeGraphicDetails from '@/components/HomeGraphicDetails/index'
import HomeList from '@/components/HomeList/index'
import List from '@/components/List'
import Classity from '@/components/Classity'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/cart',
      name: 'Cart',
      component: Cart
    },
    {
      path: '/coupon',
      name: 'Coupon',
      component: Coupon
    },
    {
      path: '/user-center',
      name: 'UserCenter',
      component: UserCenter
    },
    {
      path: '/history',
      name: 'History',
      component: History
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/youhui',
      name: 'Youhui',
      component: Youhui
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/lottery',
      name: 'Lottery',
      component: Lottery
    },
     {
      path: '/homedetail',
      name: 'HomePageDetail',
      component: HomePageDetail
    },
     {
      path: '/homegraphic',
      name: 'HomeGraphicDetails',
      component: HomeGraphicDetails
    },
     {
      path: '/homelist',
      name: 'HomeList',
      component: HomeList
    },
     {
      path: '/list',
      name: 'List',
      component: List
    },
     {
      path: '/classity',
      name: 'Classity',
      component: Classity
    }
    
  ]
})



