<template>
    <div class="page" >
        <my-header title="商品分类">
            <a href="#/cart" slot="right"></a>
        </my-header>
        <my-footer></my-footer>
        <my-content>
            <div class="list">
               <ul>
                   <li class="list-left"  @click="getProductData(4350)" >
                       <span></span>
                       <p>家装建材</p>
                   </li>
                   <li class="list-right" @click="getProductData(12349378)">
                       <span></span>
                       <p>住宅家具</p>
                   </li>
               </ul>
            </div>
            <my-pro-classity :list-data="listData" @get-id="getId"  ></my-pro-classity>
        </my-content>
    </div>
</template>
<script>
import ProClassity from './ProClassity'
export default {
  name:'classity',
  data(){
    return{
      listData:[]
    }
  },
  components:{
      "my-pro-classity":ProClassity
  },
  methods:{
    getProductData(id){
        this.$http.jsonp("http://m.mallvv.com/mobile/index.php?act=goods_class&fgc_id="+id)
        .then(res=>{
          console.log(res.body.datas['list_class'])
          this.listData=res.body.datas['list_class'];
      })
    },
    getId(id){
        this.$router.push({path:"/list",query:{id:id}})
        console.log(id);
    }
  },
  mounted(){
      this.getProductData(4350)
  }
}
</script>
<style scoped>
.page{
    background: #fff;
}
.list{
    width: 100%;
    height: 66px;
    border-bottom: 1px #eee solid;
}
.list ul{
    width: 100%;
    height: 100%;
    text-align: center
}
.list li{
    float: left;
    width: 50%;
    height: 50px;
    margin: 10px 0 5px 0;
}
.list li span{
    display: inline-block;
    width: 20px;
    height: 20px;
    background: url(http://m.mallvv.com/wap/images/list1.png) no-repeat;
    background-size: 150px 150px;
}
.list .list-right span{
    background-position: -50px 0;
}
.list .list-left{
    box-sizing: border-box;
    border-right: 1px solid #ccc;
}

</style>




