<template>
 <div class="footer">
        <ul>
            <li v-for="(data,index) in footerData">
                <router-link  :to="data.path">
                    <div><span class="icon iconfont">{{data.icon}}</span></div>
                    <div><span class="text">{{data.text}}</span></div>
                </router-link>
            </li>
          
        </ul>
    </div>
</template>

<script>
	
export default {
  name: 'my-footer',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  props:{
    footerData:{
      type:Array,
      default(){
        return [   
            {text:"首页",icon:"\ue6e4",active:0,"path":"/"},
            {text:"分类",icon:"\ue696",active:1,"path":"/classity"},
            {text:"购物车",icon:"\ue698",active:2,"path":"/cart"},
            {text:"个人中心",icon:"\ue6ce",active:3,"path":"/user-center"}
        ]
      }
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 .footer { 
 	z-index: 10;
 	position: fixed; bottom: 0; left: 0; 
        width: 100%; border-top: 1px solid #ccc;background: #efefef;
            box-shadow: 0 -1px 3px #999;
        }
.footer ul {height: 44px;}
.footer li { float: left; width: 25%; text-align: center;}
.footer a {display: block; width: 100%; height: 100%;}
.footer .icon {  line-height: 26px; font-size: 20px;}
.footer .text { line-height: 14px; font-size: 12px;  }
.footer .router-link-exact-active {color:#ffa500}
</style>



