<template>
	<div class="youhui">
		<my-header title="优惠">
			<a href="javascript:;" slot="right"><span></span></a>
		</my-header>
		<my-content>
			
			<ul class="youhui-wrap" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
				<li v-for="(data,index) in data" :key="index">
					<img v-lazy="data.img"/>
					<p>{{data.title}}</p>
					<p>{{data.subtitle}}</p>
					<p>
						<span>{{data['column_name']}}</span>
						<span>{{data.business}}</span>
					</p>
				</li>
			</ul>
		</my-content>
	</div>
</template>

<script>
	export default{
		name:'my-youhui',
		data(){
			return{
				data:[],
				allLoaded:false,
				pageNum:1
			}
		},
		methods:{
			/*loadTop() {
			  // 加载更多数据
			  this.getProductData({id:1},()=>{
		        //数据请求完成，结束下拉的状态
		        this.$refs.loadmore.onTopLoaded()
		        this.allLoaded=false;
		      })
			},
			loadBottom() {
			  // 加载更多数据
			  this.getProductData({id:1,more:true},()=>{
			  	this.allLoaded = true;
		         
		         this.$refs.loadmore.onBottomLoaded()
		       })
			},*/
			//滚动加载
			loadMore() {
			  this.loading = true;
			  this.getProductData({id:1,more:true})
			},
			getProductData(params){
		      if(params.more){
		        this.pageNum++
		      }else{ 
		        this.pageNum=1;
		      }
		      var url = `/youhui/list?type=&r=0&page=${this.pageNum}&page_size=10&publish_date=&id=0`
		      this.$http.get(url)
		        .then(res=>{
		
		          if(params.more){
		            this.data = this.data.concat(res.data.data) 
		          }else{
		             this.data = res.data.data; 
		          }         
		        })
		    }
		},
		mounted(){
			this.getProductData({id:1})
		}
	}
</script>

<style scoped>
	.youhui-wrap{
		margin: 10px;
	}
	.youhui-wrap img{
		width:100%
	}
</style>


