<template>
   <div id="home-detail">
      <my-header title="商品详情">

      </my-header>
       <my-footer></my-footer>
       <my-content>
          <my-homedetailfrom @entry-list="EntryList"></my-homedetailfrom>    
       </my-content>
   </div>
</template>

<script>
let IdData = []
export default {
    name: 'my-homepagedetail',
    data () {
        return {
            type:Array,
            TopData:[],
            storeId:"",
            store_name:"",
            id:""
        }
    },
    methods:{
            EntryList(){   
            //点击本店 进入列表页
                    this.$router.push({path:"/homelist",query:{store_id:this.storeId,store_name:this.store_name}}) 
            },
            getInitState(){
                document.body.scrollTop = 0; 
                const {goods_id}= this.$route.query
                this.id = goods_id  
                console.log(this.id)
                        var url =`/mobile/index.php?act=goods&op=goods_detail&goods_id=${goods_id}`
                        this.$http.get(url)
                        .then(res=>{
                        // console.log(res)
                            this.storeId = res.data.datas.store_info.store_id
                            this.store_name = res.data.datas.store_info.store_name
                            this.TopData = res.data.datas.goods_image.split(",")  
                    }) 
                    if(IdData.indexOf(this.id) == -1 ){
                         IdData.unshift(this.id)
                    }                               
                localStorage.setItem("goods_id",JSON.stringify(IdData))
                IdData
            }
    },
    mounted(){    
         this.getInitState()      
    }
}
</script>
<style scoped>
.banner{width:100%;height:320px;background:#fff;padding: 10px 20px;box-sizing: border-box;}
.banner img{width:100%;height:100%;}

</style>



