<template>
	<div class="lottery">
		<my-header title="幸运大抽奖">
			<a href="#/login" slot="right" @click="loginout">退出</a>
		</my-header>
		<my-content>
			<div class="turnin">
				<img src="http://files.jb51.net/file_images/article/201608/201608150908514.png" ref="rotate-img"/>
				<img src="http://files.jb51.net/file_images/article/201608/201608150908502.png" @click="turn"/>
				<div class="preventClick" ref="preventClick"></div>
			</div>
		</my-content>
	</div>
</template>

<script>
	import { Toast } from 'mint-ui';
	let userID = localStorage.getItem("userID")
	//优惠券
	let coupon = localStorage.getItem('coupon')||[];
	
	export default {
		name:'my-lottery',
		methods:{
			turn(){
				let rotateImg = this.$refs["rotate-img"];
				let deg = 0;
				let endtime = parseInt(Math.random()*1000+1000)
				let rotateDeg = 0;
				const perDeg = 360/7;
				if(!userID.length){
					Toast("请登录")
					this.$router.push('/login')
				}else{
					let preventClick = this.$refs.preventClick;
					preventClick.style.display = 'block'
					if(coupon.length>=3){
						Toast('对不起，您的抽奖次数已用完');
					}else{
						let timer = setInterval(function(){
							deg++;
							rotateImg.style.transform=`rotate(${deg}deg)`;
							if(deg>=endtime){
								
								preventClick.style.display = 'none'
								deg = endtime;
								clearInterval(timer);
								rotateDeg = deg%360;
								if(rotateDeg<=perDeg*1){
								  	Toast("恭喜您中4999元");
								  	coupon.unshift('https://f11.baidu.com/it/u=354605451,612639255&fm=72')
								}else if(rotateDeg<=perDeg*2){
								  	Toast("恭喜您中免单50元");
								  	coupon.unshift('https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1554112936,3538605106&fm=26&gp=0.jpg')
								}else if(rotateDeg<=perDeg*3){
								  	Toast("恭喜您中免单10元");
								  	coupon.unshift('https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=1335936560,3348489669&fm=26&gp=0.jpg')
								}else if(rotateDeg<=perDeg*4){
								  	Toast("恭喜您中免单5元");
								  	coupon.unshift('https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=822011080,3558108986&fm=26&gp=0.jpg')
								}else if(rotateDeg<=perDeg*5){
								  	Toast("恭喜您中免单免息服务");
								  	coupon.unshift('https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=1225678232,98680042&fm=26&gp=0.jpg')
								}else if(rotateDeg<=perDeg*6){
									Toast("恭喜您中提高白条金额");
									coupon.unshift('https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=1748002814,2775784742&fm=26&gp=0.jpg')
								}else if(rotateDeg<=perDeg*7){
								  	Toast("抱歉，您未中奖");
								}
								//设置优惠券
								localStorage.setItem("coupon",JSON.stringify(coupon))
							}
						},1)
						
					}
				}
			},
			loginout(){
				coupon.length=0;
				localStorage.clear();
				
			}
		},
		
	}
	
	
</script>

<style scoped>
	
	.lottery .content{
		padding:60px 10px 0;
		text-align: center;
	}
	.lottery .content .turnin{
		position: relative;
		background: url("http://files.jb51.net/file_images/article/201608/201608150908513.jpg") no-repeat;
		background-size: 100% 100%;
	}
	.lottery .content .turnin img:nth-child(1){
		width: 98%;
	}
	.lottery .preventClick{
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,0);
		display: none;
	}
	.lottery .content .turnin img:nth-child(2){
		position: absolute;
		left: 50%;
		top: 50%;
		margin-left: -16.5%;
		margin-top:-18%;
		width: 30%;
		height: 36%;
	}
</style>


